/* SearchPage.css */
.search-container {
    padding: 20px;
    text-align: center;
  }
  
  .search-input {
    padding: 10px;
    margin-right: 10px;
    width: 200px;
  }
  
  .search-button, .filter-button {
    padding: 10px 15px;
    margin-right: 10px;
    background-color: #007bff;
    color: white;
    border: none;
    cursor: pointer;
  }
  
  .filter-buttons {
    margin-top: 20px;
  }
  
  .link-to-communicate {
    display: inline-block;
    margin-top: 20px;
    padding: 10px;
    background-color: #28a745;
    color: white;
    text-decoration: none;
  }
  .filter-buttons :hover {
    background-color: #01090a;
  }

  .search-button :active {
    background-color: #01090a;
  }